* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
  }
  a{
    text-decoration: none;
    color:black;
  }a:hover{
    color: burlywood;
  }
  
  /* Here we style the body */
  body {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
    background-color: skyblue;
    color: white;
  }
  /* Here we style the h3 Tag */
  h3{
    color:black;
  }
  
  /* Here we style the div */
  #root{
    width: 80%;
    border-right: 1px solid #323232;
    border-left: 1px solid #323232;
  }
  
  /* Here We style The navigation bar!!!!*/
  .navbar{
    align-items: center;
    display: flex;
    height: 100px;
    padding: 0px 20px;
    justify-content: space-between;
    border-bottom: 1px solid #323232;
  }
  .right-nav{
    display: flex;
    align-items: center;
  }
  .right-nav h5{
    color: black;
    margin-right: 10px;
  }
  .addhabit-btn{
    cursor: pointer;
    color: blue;
    background-color: black;
    padding: 8px 20px;
    border-radius: 7px;
  }
  
  .modal-content{
    background-color:violet ;
  }
  .modal-header{
    border: none;
  }
  .modal-footer{
    border: none;
  }
  
  
  /* Here we style the Habits component */
  .habits{
    width: 100%;
  }
  
  /* Here we style Habit present under Habits!!!!! */
  .habit{
    width: 100%;
    height: 100px;
    border-bottom: 1px solid #323232;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
  }
  .habit:hover{
    background-color: goldenrod;
  }
  .habit-left{
    display: flex;
    align-items: center;
  }
  .habit-left i{
    color: #2AA8D0;
    padding: 10px;
    border-radius: 50%;
    background-color: black;
    margin-right: 10px;
  }
  .day-complete{
    color: black;
    font-size:small;
  }
  .habit-right{
    display: flex;
    align-items: center;
  }
  .log-btn{
    text-decoration: none;
    cursor: pointer;
    background-color: black;
    padding: 10px;
    border-radius: 7px;
    margin-right: 10px;
    color: blue;
    border: none;
  }
  .log-btn:hover{
    color: white;
  }
  .log-btn i{
    margin-right: 5px;
  }
  .fa-trash{
    cursor: pointer;
    padding: 10px;
    border-radius: 7px;
    background-color: black;
  }
  
  
  /* Here We style the Week View Container!!!!!!!! */
  .days-container{
    margin: 20px 0px;
    display: flex;
    justify-content: space-around;
  }
  /*  Here We style day view component!!!!!!! */
  .day-container{
    height: 100px;
    width: 150px;
    background-color: black;
    border-radius: 20px;
    padding: 5px;
  }

  p{
    margin: 0%;
  }
/* Here we style Icon */
  .circle-icon{
    font-size: 24px;
    margin: 5px 11px;
    cursor: pointer;
  }
  /* Here we style the ACtive Part */
  .active{
    color:Red ;
  }
  /*@@@@@@@Debanshu Habit-Tracker Style Ends@@@@@@@@@*/